.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: wheat;
    align-items: center;
    
    width: 500px;
}

.image{
    border-radius: 40%;
    width: 100px;
    height: 100px;
}